@import "../../theme.scss";

.rahmen-content {
  height: 100%;
  display: flex;
  flex-direction: column;

  .new-rahmen {
    height: 66px;
    padding: 0 15px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    display: flex;

    .new-rahmen-btn {
      align-self: center;

      button {
        padding: 0 20px;
        height: 40px;
        background: $bg-color;
        border-radius: 3px;
        border: 0;
        font-size: 14px;

        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 20px;
        cursor: pointer;
      }
    }

    .rahmen-num {
      align-self: center;
      margin-left: 50px;

      .rahmen-num-icon {
        height: 100%;
        display: inline-block;
        font-size: 32px;
        vertical-align: middle;
        margin-right: 10px;
        padding-top: 5px;
        i {
          font-size: 32px;
        }
      }

      .num-text {
        float: right;
        height: 100%;
        padding: 5px 3px 0 3px;
        text-align: left;

        .text {
          margin: 0;
          height: 19px;
          font-size: 13px;

          font-weight: 400;
          color: rgba(1, 1, 1, 0.6);
          line-height: 19px;
        }

        .num {
          margin: 0;
          height: 24px;
          font-size: 16px;

          font-weight: 400;
          color: rgba(25, 25, 25, 1);
          line-height: 24px;
        }
      }
    }
  }

  .rahmen-operation {
    height: 45px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    padding: 0 15px;
    display: flex;

    .checkAll {
      align-self: center;
      display: flex;

      .check-rahmen {
        margin-right: 10px;
        cursor: pointer;
        font-size: 20px;
      }

      .check-rahmen-opt {
        span {
          background: $bg-color;
          border: 2px solid $bg-color;

          i {
            visibility: initial;
          }
        }
      }

      .checkAll-text,
      .invert-Selection {
        font-size: 14px;
        color: rgba(2, 2, 2, 1);
        line-height: 20px;
        margin-right: 24px;
      }

      .invert-Selection {
        cursor: pointer;
        color: $bg-color;
      }
    }

    .rahmen-sort {
      position: relative;
      height: 30px;
      align-self: center;
      margin-right: 24px;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;
      display: flex;
      align-self: center;

      .sort {
        align-self: center;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        padding: 0 10px;
        height: 100%;
        line-height: 30px;
        cursor: pointer;

        i {
          margin-left: 10px;
        }

        &:hover {
          background-color: $hover;
        }
      }

      .sort-list {
        width: 220px;
        position: absolute;
        top: 31px;
        right: 0;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
        border-radius: 3px;
        border: 1px solid rgba(233, 233, 233, 1);
        padding: 12px 0;
        z-index: 9;

        ul li {
          cursor: pointer;
        }

        ul li span {
          display: block;
          height: 30px;
          font-size: 14px;

          color: rgba(2, 2, 2, 1);
          line-height: 30px;
          padding-left: 15px;

          i {
            visibility: hidden;
            margin-right: 15px;
          }

          .check-icon {
            visibility: initial;
            color: $bg-color;
          }
        }

        ul li:hover {
          background: $hover;
        }
      }
    }

    .rahmen-delete {
      align-self: center;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;

      span {
        display: block;
        height: 30px;
        padding: 0 10px;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        font-size: 12px;

        font-weight: 400;
        color: rgba(31, 31, 31, 1);
        line-height: 30px;
        cursor: pointer;

        i {
          margin-right: 10px;
        }
      }
    }
  }

  .rahmen-main {
    display: flex;
    flex: 1;
    overflow-y: auto;
    flex-wrap: wrap;
    align-items: flex-start;
    padding-bottom: 120px;

    .list {
      padding: 5px 10px;
    }

    .rahmen-list {
      display: flex;
      flex-direction: column;
      position: relative;
      height: 100%;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 2px 6px 0px #ebebeb;
      border: 2px solid rgba(255, 255, 255, 1);
      border-radius: 4px;
      cursor: pointer;

      .rahmenlist-check {
        position: absolute;
        display: block;
        visibility: hidden;
        top: 5px;
        right: 5px;
        font-size: 20px;
        background: rgba(255, 255, 255, 1);
        border-radius: 5px;
        color: $bg-color;
      }

      .rahmen-cover {
        height: 75%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px solid #e4e4e4;

        .cover-img {
          position: relative;
          width: 146px;
          height: 198px;
          box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.3);
          padding: 10px 8px;
          overflow: hidden;

          .back-img {
            display: block;
            border: 1px solid #e9e9e9;
            height: 100%;
            width: 100%;
            background: url("../../images/framelogo.png") no-repeat;
            background-position: center;
            background-size: 100% 100%;
          }
        }
      }

      .rahmen-name-series {
        height: 25%;
        padding-left: 14px;
        padding-right: 14px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        p {
          margin: 0;
        }

        .name_info {
          height: 21px;

          .name {
            display: inline-block;
            width: 150px;
            font-size: 16px;

            color: rgba(0, 0, 0, 1);
            line-height: 21px;
          }

          .admin {
            float: right;
            background-color: $bg-color;
            color: #fff;
            font-size: 14px;
            border-radius: 3px;
          }
        }

        .series {
          height: 21px;
          font-size: 14px;

          color: rgba(179, 180, 183, 1);
          line-height: 21px;
        }
      }

      .rahmen-list-more {
        position: absolute;
        right: 15px;
        bottom: 15px;

        .more-box {
          position: relative;

          span {
            font-size: 20px;
            color: #000;
            padding: 0 3px;
            border-radius: 3px;

            &:hover {
              background-color: $hover;
            }
          }

          .more-menu {
            position: absolute;
            top: 33px;
            right: -20px;
            width: 180px;
            background: rgba(255, 255, 255, 1);
            box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
            border-radius: 3px;
            padding: 12px 0;
            border: 1px solid rgba(233, 233, 233, 1);
            overflow: hidden;
            z-index: 9;

            ul li span {
              height: 42px;
              line-height: 42px;
              font-size: 14px;

              color: rgba(2, 2, 2, 1);

              i {
                font-size: 20px;
                margin: 0 16px;
                vertical-align: middle;
              }
            }

            ul li:hover {
              background-color: $hover;
            }

            .upfile {
              position: relative;

              input {
                position: absolute;
                height: 42px;
                right: 0;
                top: 0;
                opacity: 0;
              }
            }
          }
        }
      }

      &:hover {
        border-color: $bg-color;
      }

      &:hover .rahmenlist-check {
        visibility: initial;
      }
    }

    .rahmen-list-check {
      border-color: $bg-color;

      .rahmenlist-check {
        visibility: initial;
      }
    }
  }

  .cover-Mask {
    position: fixed;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.2);
    z-index: 199;
    text-align: center;

    &::before {
      content: "";
      height: 100%;
      display: inline-block;
      vertical-align: middle;
    }

    .cover-inner {
      width: 600px;
      background-color: #fff;
      display: inline-block;
      vertical-align: middle;
      text-align: left;
      padding: 20px;

      .preview_picture {
        overflow: hidden;

        .preview_left {
          float: left;

          .show-img {
            height: 300px;
            width: 400px;
          }

          .preview_btn {
            display: flex;
            justify-content: space-around;
            margin-top: 15px;

            span {
              padding: 5px 15px;
              color: #fff;
              cursor: pointer;
              background-color: $bg-color;
              border-radius: 5px;
            }
          }
        }

        .preview_right {
          float: right;
          width: 145px;
          height: 300px;
          padding-left: 20px;
          border-left: 1px solid #333;

          .before {
            border: 5px solid #fff;
            box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
            width: 126px;
            height: 174px;
            overflow: hidden;
          }
        }
      }
    }
  }
  .icon-xuanzhong1{
    color: $bg-color;
  }
}